.customBtn {
  margin: 50px;
}

.btn {
  display: inline-block;
  align-content: center;
  align-items: center;
  min-width: 130px;
  height: 40px;
  border: none;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  text-transform: capitalize;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;

  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.342),
    2px 2px 10px 0px rgba(0, 0, 0, 0.555);
  outline: none;
}

.btn:nth-child(1) {
  background-color: #137708;
  border: 1px solid #137708;
}

.btn:nth-child(2) {
  background-color: #d47c09;
  border: 1px solid #d47c09;
}

.btn:nth-child(3) {
  background-color: #d40902;
  border: 1px solid #d40902;
}
.btn:not(:first-child) {
  margin-left: 25px;
}

.btn:after {
  position: absolute;
  content: "";
  width: 0;
  height: 100%;
  top: 0;
  left: 0;
  direction: rtl;
  z-index: -1;
  /* box-shadow: -2px -1px 10px 0px rgba(0, 0, 0, 0.6); */
  transition: all 0.3s ease;
}

.btn:hover {
  text-transform: uppercase;
  font-weight: 600;
}

.btn:hover:after {
  left: auto;
  right: 0;
  width: 100%;
}

.btn:active {
  top: 2px;
}
